import { Component, Input, Output } from '@angular/core';
import { ColorService } from './color.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styles: ['.row { background: #DCDCDC; padding: 20px; }']

})
export class AppComponent {
  @Output() color: any;
  pricesMaker = [];

  constructor(private service: ColorService) {
  }

  ngOnInit(): void {
    // this.service.getColors().subscribe((response) => {
    //   //("Response", response);
    //   this.color = response[0].colors;
    // });
  }

  onBasketIncluded(event: any) {
    if (event.target.checked === true) {
      this.pricesMaker.push(event.target.defaultValue);
    }
  }

  onBlowerIncluded(event: any) {
    if (event.target.checked === true) {
      this.pricesMaker.push(event.target.defaultValue);
    }
  }

  onDeliveryIncluded(event: any) {
    if (event.target.checked === true){
      this.pricesMaker.push(event.target.defaultValue);
    }
  }

  onPricesAccepted() {
    console.log(this.pricesMaker);
  }

}
